<template>
    <div class="industry">
        <p-list :listData="lists" @handleSel="select"></p-list>
    </div>
</template>

<script>
    import pList from '../../components/p-list.vue';
    import { publicPost } from '../../js/api/index.js';
    import getApiUrl from '../../js/api/apiUrlList';

    export default {
        name: 'industry',
        components: { pList },
        data () {
            return {
                lists: [],
                selNum: 0
            };
        },
        created () {
            this.$router.setTitle(this.$t('title.industry'));
            this.getList();
        },
        methods: {
            getList () {
                let params = {},
                    self = this;
                params.type = 1;
                publicPost(getApiUrl('getTypes'), params).then((res) => {
                    self.lists = res;
                }).catch(() => {
                    console.warn('error');
                });
            },
            select (item) {
                this.$root.industry = item;
                this.$router.go(-1);
            }
        }
    };
</script>

<style rel="stylesheet/scss" lang="scss">
    @import "../../scss/variables";

    .industry {
        width: 100%;
        background: #f2f2f4;
    }

</style>
